import React, { Component } from 'react'
import withStyles from '@material-ui/core/styles/withStyles';
import { Link,withRouter } from 'react-router-dom';
import { CssBaseline, Grid} from '@material-ui/core';
import Navbar from './NavBarL'
import Button from '@material-ui/core/Button';

//import images 
import sepro from '../resources/sepro.png';
import cundinamarca from '../resources/uc.png';
import unal from '../resources/unal.png';
import service from '../resources/servicios.png';
import equipo from '../resources/equipo.png';


import {connect} from 'react-redux'
//import openList from '../redux/actions/List'

const styles = theme => ({
  
   root: {
      display: 'flex',
      flexGrow: 1,
      //backgroundImage: "url(http://consejerosviajeros.com/wp-content/uploads/2019/06/0.-Portada-Dubrovnik-1024x486.jpg)",
      backgroundPosition: 'center',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      overflow: 'hidden',
      backgroundColor: "#fafdff"
   },

   appBarSpacer: {
      marginBottom: theme.spacing(12)
   },
   
   content: {
      flexGrow: 0,
      height: '100vh',
      overflow: 'auto',
      position: 'relative'
   },
   card:{
      backgroundColor: "#3a8ff0"
   },
   main:{
      backgroundImage: "url(https://cdn.pixabay.com/photo/2017/02/05/04/21/keppel-2039122_960_720.jpg)",
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      height: '600px',
      fontFamily: 'Lora',
      textAlign: "center",
      color: 'white',
      justifyContent: "center",
      
      alignItems: "center"
   },
   main_title:{
      fontSize: "120px",
   },
   main_text:{
      fontSize: "35px",
   },
   black:{
      backgroundColor: "#ffff",
      height: '400px',
      textAlign: "center",
      justifyContent: "center",
      color: 'black',
   },
   black_images:{
      marginTop: '35px'
   }
   ,
   other_title:{
      fontSize: "50px",
   },
   other_text:{
      fontSize: "20px",
      paddingLeft: "40px",
      width: "90%"
   },
   blue:{
      backgroundColor: "#d0def7",
      height: '400px',
      textAlign: "center",
      color: 'black',
      justifyContent: "center",
   },
   footer:{
      paddingTop:'120px',
      paddingLeft :'50px',
      fontSize: '24px',
      textAlign: "center",
      flexGrow: 1,
   },
   extendedIcon: {
      marginRight: theme.spacing(1),
   },
   textoButton: {
      color: 'white',
      textDecorationLine: 'none',
   },
});

class Landing extends Component{
   constructor(props){
      super(props);
      this.state = {
        isDataLoaded: false,
        
      };
      this.openl = this.openl.bind(this)
   }
   openl(){
      console.log(this.props)
   }
   render(){
      const { classes } = this.props;
      return(
         <div className={classes.root}>
            <Grid container direction="row" justify="flex-start" alignItems="flex-start">
               <Grid item xs={12} >
                  <Navbar/>
                  <CssBaseline/>        
               </Grid>
               
               <Grid container  direction="column" justify="flex-start" className={classes.main} >
                  <h1 className={classes.main_title}>Intermodalidapp</h1>
                  <p className={classes.main_text}>Soluciones más allá de las fronteras</p>
               </Grid>
               
               <Grid container  direction="row"  justify="flex-start" className={classes.black} >
                  <Grid item xs={12} sm={6} md={6} className={classes.black_images}>
                     <img src={service} alt="cu" width="300" height="300"/>
                  </Grid>
                  <Grid item xs={12} sm={6} md={6}>
                     <h3 className={classes.other_title}>Servicios</h3>
                     <div className={classes.other_text}>
                        
                           Esta aplicacion tiene como finalidad 
                           presentar a los generadores de carga 
                           las diferentes alternativas para el 
                           transporte de su mercancia de manera intermodal.
                     </div>
                     <Link className={classes.textoButton} to="/user">
                        <Button
                           color="primary"
                           className={classes.submit}
                           onClick={this.openl}>
                           Servicios
                        </Button>
                     </Link>
                     
                  </Grid>
               </Grid>
               <Grid container  direction="row"  justify="flex-start" className={classes.blue}>
                  
                  <Grid item xs={12} sm={6} md={6}>
                     <h3 className={classes.other_title}>Equipo</h3>
                     <div className={classes.other_text}>
                        
                        Este desarrollo fue realizado por el 
                        grupo de investigación Sepro, un grupo 
                        interdisciplicinar que aborda desde 
                        diferentes puntos de vista los temas 
                        relaciones con la logistica y la cadena de suministro.
                     </div>
                        
                  </Grid>
                  <Grid item xs={12} sm={6} md={6} className={classes.black_images}>
                     <img src={equipo} alt="cu" width="300" height="300"/>
                  </Grid>
               </Grid>
               
               <Grid container direction="column" justify="center"  className={classes.black}>
                  <Grid container direction="row"></Grid>
                     <h2>Colaboradores</h2>
                  <Grid container direction="row" alignItems="center">
                  <Grid item xs={12} sm={4}>
                     <img src={sepro} alt="sepro" width="250" height="210"/>
                  </Grid>
                  <Grid item xs={12} sm={4}>
                     <img src={cundinamarca} alt="cu" width="210" height="220"/>
                  </Grid>
                  <Grid item xs={12} sm={4}>
                     <img src={unal} alt="cu" width="220" height="230"/>
                  </Grid>
                  </Grid>
               </Grid>     
               
               
            </Grid>        

          
                  
        </div>
      );
    }
}
const mapStateToProps = (state) => {
   return {
      listCities: state.ListCities,
   };
};
const mapDispatchToProps = {
   //openList,
}


export default withRouter(
   withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(Landing))
);