import React, { Component } from 'react'  
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import Grid from '@material-ui/core/Grid';

import {
   MuiPickersUtilsProvider,
   KeyboardTimePicker,
   KeyboardDatePicker,
} from '@material-ui/pickers';

import { withRouter } from 'react-router-dom';

// Redux
import {connect} from 'react-redux'
import {change} from '../redux/actions/DateTime'


class DateTime extends Component {
    
   constructor(props){
      super(props);
      this.state = {
         //selectedDate: new Date('2019-10-31T09:00:00')
         selectedDate: new Date()
      }
      this.ChangeOption = this.ChangeOption.bind(this)
   }
   ChangeOption(event){
      //console.log(event)
      
      this.setState({
         selectedDate: event
      })
      if (this.props.labelDate === 'Fecha Salida'){
         this.props.change(['salida',event])
      }else{
         this.props.change(['llegada',event])
      }
      
   }
    

   render(){
      const {DateTime} = this.props;
      //console.log("date in dateTime(redux): ",DateTime)
      return(
         <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid container justify="space-around">
               <KeyboardDatePicker
                  disableToolbar
                  variant="inline"
                  format="MM/dd/yyyy"
                  margin="normal"
                  id="date-picker-inline"
                  label={this.props.labelDate}
                  value={this.state.selectedDate}
                  onChange={this.ChangeOption}
                  KeyboardButtonProps={{'aria-label': 'change date'}}
               />
               
               <KeyboardTimePicker
                  margin="normal"
                  id="time-picker"
                  label={this.props.labelTime}
                  value={this.state.selectedDate}
                  onChange={this.ChangeOption}
                  KeyboardButtonProps={{'aria-label': 'change time'}}
               /> 
            </Grid>
         </MuiPickersUtilsProvider>
         
      )
   }
}

const mapStateToProps = (state) => {
   return {
      DateTime: state.DateTime,
   };
};

const mapDispatchToProps = (dispatch) => {
   return{
      change: (date) => {dispatch(change(date))}
   }
}
export default withRouter(connect(mapStateToProps,mapDispatchToProps)(DateTime));