import React, { Component } from 'react'
import { withStyles } from '@material-ui/styles';
import { withRouter } from 'react-router-dom';
import { fade } from '@material-ui/core/styles/colorManipulator';
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import info from '../resources/question.svg'
import MenuIcon from '@material-ui/icons/Menu'

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import AccountCircle from '@material-ui/icons/AccountCircle';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Badge from '@material-ui/core/Badge';
import MailIcon from '@material-ui/icons/Mail';
import InputBase from '@material-ui/core/InputBase';
import MoreIcon from '@material-ui/icons/MoreVert';

//drawer
import Drawer from '@material-ui/core/Drawer';
import Complement from './ComplementD'

// alternatives
//import Card from './CardAlternatives'  

import SearchIcon from '@material-ui/icons/Search';



const drawerWidth = 660;

const styles = theme => ({
    grow: {
        flexGrow: 1,
        
    },
    
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        display: 'none',
        [theme.breakpoints.up('sm')]: 
        {
            display: 'block',
        },
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    },
    searchIcon: {
        width: theme.spacing(7),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 6),
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: 200,
        },
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
        
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    textField:{
      color :"#ffff"
    },
    // drawer
   drawer: {
      width: drawerWidth,
      flexShrink: 0,
   },
   drawerPaper: {
      width: drawerWidth,
   },
   searchA:{
      justifyContent: 'center',
      alignItems: 'center',
      display: 'flex',
   },cards:{
      justifyContent: 'center',
      paddingLeft: '20px',
      display: 'block',
   },info:{
      backgroundColor: "#ffff"
   }
  

});
class NavBar extends Component{
   constructor(props){
      super(props);
      this.state = {
         open: false,
         anchorEl: null,
         mobileAnchorEl: null,
         MobileOpen: false,
         isMobileOpen: false,
         MenuOpen: false,
         SetMenuOpen: false,
         //open drawer
         openD: false,
         SetOpenD: false,
         // data in drawer
         origin: '',
         destination: '',
         cards: []
      }
      this.handleProfileMenu = this.handleProfileMenu.bind(this);
      this.handleMobileMenu = this.handleMobileMenu.bind(this);
      this.LinkProfile  = this.LinkProfile.bind(this);
      this.LinkLogout = this.LinkLogout.bind(this);
      //open drawer 
      this.handleDrawerOpen =  this.handleDrawerOpen.bind(this);
      // draw II or I Alternative
      this.button1 = this.button1.bind(this);
      this.button2 = this.button2.bind(this);
   }
   
   
    // Open Mobile menu
   handleMobileMenu(event) {
      this.setState({
         mobileAnchorEl: event.currentTarget,   
         MobileOpen: !this.state.isMobileOpen,
         isMobileOpen: !this.state.MobileOpen
      })
   }

   // Open profile menu
   // anchor is the element used to set the position of the menu 
   handleProfileMenu(event) {
      this.setState({
         anchorEl: event.currentTarget,   
         MenuOpen: !this.state.SetMenuOpen,
         SetMenuOpen: !this.state.MenuOpen
      })
   }
   LinkProfile(event){
      this.setState({
         MenuOpen: !this.state.SetMenuOpen,
         SetMenuOpen: !this.state.MenuOpen
      })
   }
   LinkLogout(){
      console.log("logout")
      this.setState({
         MenuOpen: !this.state.SetMenuOpen,
         SetMenuOpen: !this.state.MenuOpen
      })
      this.props.history.push("/")
   }

   handleDrawerOpen(event) {
      console.log("drawer open!!!")
      if (localStorage.getItem('view') === 'user'){
         this.setState({
            openD: !this.state.SetOpenD,
            SetOpenD: !this.state.openD
         })
      }
      
      console.log(this.openD," ",this.SetOpenD)
   }
   
   // draw alternative I
   async button1(){
      await localStorage.setItem('route', '1');
   }
   // draw alternative II 
   async button2(){
      await localStorage.setItem('route', '2');
   }

   openInfo(){

   }

   render(){
      
      const menuId = 'primary-search-account-menu';
      const renderMenu = (
         <Menu
            anchorEl={this.state.anchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={menuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={this.state.MenuOpen}
            onClose={this.handleMenuClose}>
            <MenuItem onClick={this.LinkProfile}>Perfil</MenuItem>
            <MenuItem onClick={this.LinkLogout}>Cerrar Sesión</MenuItem>
         </Menu>
      );
      const mobileMenuId = 'primary-search-account-menu-mobile';
      const renderMobileMenu = (
         <Menu
            anchorEl={this.state.mobileAnchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={mobileMenuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={this.state.MobileOpen}
            onClose={this.handleMobileMenuClose}
         >
            <MenuItem onClick={this.handleMobileMenu}>
               <IconButton aria-label="show 1 new mails" color="inherit">
                  <Badge badgeContent={1} color="secondary">
                     <MailIcon />
                  </Badge>
               </IconButton>
               <p>Mensages</p>
            </MenuItem>
            <MenuItem onClick={this.handleMobileMenu}>
               <IconButton aria-label="show 11 new notifications" color="inherit">
                  <Badge badgeContent={2} color="secondary">
                     <NotificationsIcon />
                  </Badge>
               </IconButton>
               <p>Notificaciones</p>
            </MenuItem>

            <MenuItem onClick={this.handleMobileMenu}>
               <IconButton
                  aria-label="account of current user"
                  aria-controls="primary-search-account-menu"
                  aria-haspopup="true"
                  color="inherit"
               >
                  <AccountCircle />
               </IconButton>
               <p>Profile</p>
            </MenuItem>
         </Menu>
      );
      const { classes } = this.props;
      return(
         <div className={classes.grow}>
               <AppBar position="static" color="primary">
                  <Toolbar>
                     <IconButton
                        edge="start"
                        className={classes.menuButton}
                        color="inherit"
                        aria-label="open drawer"
                        onClick={this.handleDrawerOpen}
                     >
                        <MenuIcon/>
                     </IconButton>
                     <Typography className={classes.title} variant="h6" noWrap>
                        IMA
                     </Typography>
                     <div className={classes.search}>
                           <div className={classes.searchIcon}>
                              <SearchIcon/>
                           </div>
                           <InputBase
                              placeholder="Search…"
                              classes={{
                                 root: classes.inputRoot,
                                 input: classes.inputInput,
                              }}
                              inputProps={{ 'aria-label': 'search' }}
                           />
                     </div>
                     
                     <div className={classes.grow} />
                     <div className={classes.sectionDesktop}>
                        <IconButton 
                           aria-label="show 1 new mails" 
                           color="inherit"
                           onClick={this.openInfo}
                           >
                           <img src={info} width="30px" />
                        </IconButton>

                        <h4> {localStorage.getItem('username')} </h4>
                        <IconButton
                           edge="end"
                           aria-label="account of current user"
                           aria-controls={menuId}
                           aria-haspopup="true"
                           onClick={this.handleProfileMenu}
                           color="inherit"
                           >
                           <AccountCircle style={{ fontSize: 35 }}/>
                        </IconButton>
                           
                     </div>
                     
                     <div className={classes.sectionMobile}>
                        <IconButton
                           aria-label="show more"
                           aria-controls={mobileMenuId}
                           aria-haspopup="true"
                           onClick={this.handleMobileMenu}
                           color="inherit"
                           >
                           <MoreIcon />
                        </IconButton>
                     </div>
                  </Toolbar>
               </AppBar>

               <Drawer
                  className={classes.drawer}
                  variant="persistent"
                  anchor="left"
                  open={this.state.openD}
                  classes={{ paper: classes.drawerPaper}}>
                  <Complement info={this.props.info} close={this.handleDrawerOpen}/>
                  
               </Drawer>
               


               
               {renderMobileMenu}
               {renderMenu}
         </div>
         
      )
   }
}
export default withRouter(withStyles(styles)(NavBar))