import React, { Component } from 'react'
import Navbar from './NavBar'
import { CssBaseline, Grid} from '@material-ui/core';
import Card from './CardServices'
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';
import Container from '@material-ui/core/Container';
import { URLGRAPH } from '../constants'
import Paper from '@material-ui/core/Paper';
//Form

//Dialog
import DialogService from './DialogService'
import FormControl from '@material-ui/core/FormControl';

// plus 
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';

// Redux
import {connect} from 'react-redux'


const styles = theme => ({
    root: {
      display: 'flex',
      flexGrow: 1,
      backgroundColor: theme.palette.grey['100'],
      overflow: 'hidden',
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
      flexGrow: 0,
      height: '100vh',
      overflow: 'auto',
    },
    container: {
      paddingTop: theme.spacing(10),
      paddingBottom: theme.spacing(2),
      height: '100vh',
    },
    columna: {
      direction: 'row',
    },
    formControl: {
      margin: theme.spacing(2),
      marginTop:'30px'
    },
    cards:{
      marginTop:'30px'
    }
    ,
    labelPublico: {
      marginBottom: '1em',
      marginTop: '2em'
    },
    // for plus
    fab: {
      position: 'relative',
      marginTop:'30px',
      marginLeft:'90px',
      justifyContent: 'center',
      textAlign: 'center',
      bottom: theme.spacing(2),
      right: theme.spacing(2),
    },
   menu_service:{
      fontFamily: 'Lora',
   },
   bTrain:{
      color: 'secondary',
   },
   TrainE:{
      backgroundColor: '#34eb6e',
      width: '140px',
      height:'80px',
      fontFamily: 'Lora',
      fontSize: "40px",
      justifyContent: 'center',
      textAlign: 'center'
   },
   TrainF:{
      backgroundColor: '#e34242',
      width: '140px',
      height:'80px',
      fontFamily: 'Lora',
      fontSize: "40px",
      justifyContent: 'center',
      textAlign: 'center'
   }
   ,paper_info:{
      width: '310px',
      height:'90px',
      fontFamily: 'Lora',
      textAlign: 'center',
      fontSize: "20px",
   },paper_plus:{
      marginBottom: '-12px',
      marginLeft: '20px'
   }
})

class Services extends Component {
    
    constructor(props){
         super(props);
         this.state = {
            cards: [],
            id_user: 0,
            id: 0,
            email: '',
            Title:' ',
            Type: 'Carga',
            Description: ' ',
            Capacity: 0,
            Places: 0,
            Cost: ' ',
            Origin :' ',
            Destination :' ',
            Transport :' ',
            Ubication: '',
            DateStart: '',
            DateEnd: '',
            open: false,
            //open to show the train
            openT: false ,
            openO: false,
            setOpenO:false,
            openD: false,
            setOpenD:false,
            openC: false,
            setOpenC: false,
            denseT: {
                one: false,
                two: false,
                three: false,
                four: false
            },
            denseO:{
                one: false,
                two: false,
                three: false,
                four: false,
                five: false,
                six: false
            },
            denseD:{
               one: false,
               two: false,
               three: false,
               four: false,
               five: false,
               six: false
            }
        }
        
      // Obtain data from InputText
      this.sendReq = this.sendReq.bind(this);

      // update the Service Data
      this.sendUpdate = this.sendUpdate.bind(this);
      this.handleClose = this.handleClose.bind(this);
      this.InputChange = this.InputChange.bind(this);
      // Open check - Transport
      this.handleClick = this.handleClick.bind(this);
      this.handleClickO = this.handleClickO.bind(this);
      // Change Transport
      this.changeState = this.changeState.bind(this);
      this.changeOrigin = this.changeOrigin.bind(this);
      this.changeDate =   this.changeDate.bind(this);
      //open the train
      this.OpenTrain = this.OpenTrain.bind(this);
      
   }

   async InputChange(event) {
      const target = event.target.id;
      const value = event.target.value;
      
      //console.log(target,"InputChange: ", value)
      switch(target){
         case "title":await this.setState({ Title: value}); break;
         case "descrip":await this.setState({ Description: value}); break;
         case "capacity": await this.setState({ Capacity: value}); break;
         case "places":await this.setState({ Places: value}); break;
         case "costo":await this.setState({ Cost: value});break;
         default:await this.setState({ Ubication: value});break;
      }
      
   }
    // Change the Transport 
   async changeState(event){
      const transport = event.target.value
      
      switch(transport){
         case "terrestre":
         await this.setState({
            denseT:{
               one: !this.state.denseT.one,two: this.state.denseT.two,
               three: this.state.denseT.three,four: this.state.denseT.four
            },Transport:transport
         })
         break;
         case "ferreo":
            await this.setState({
               denseT:{
                     one: this.state.denseT.one,
                     two: !this.state.denseT.two,
                     three: this.state.denseT.three,
                     four: this.state.denseT.four
               },Transport:transport
            })
         break;
         case "fluvial":
            await this.setState({
               denseT:{
                     one: this.state.denseT.one,
                     two: this.state.denseT.two,
                     three: !this.state.denseT.three,
                     four: this.state.denseT.four
               },Transport:transport
            })
         break;
         default:
            await this.setState({
               denseT:{
                     one: this.state.denseT.one,
                     two: this.state.denseT.two,
                     three: this.state.denseT.three,
                     four: !this.state.denseT.four
               },Transport:transport
            })
         break;
      }
      //console.log(this.state)
   }

   async changeOrigin(event){
      const {listCities} = this.props;
      //console.log(listCities)
      var t = 0
      var entry = false
      var len = Object.keys(listCities).length
      for(var i = 0 ; i < len;i++){
         //console.log(listCities[i].name,i)
         if (listCities[i].option === true & entry === false){
            t = 1
            entry = true
            //console.log("o: ",listCities[i].name)
            await this.setState({Origin: listCities[i].name})
         }
         if (listCities[i].option === true & i !== t){
            t = i
            //console.log("d: ",listCities[i].name)
            await  this.setState({Destination: listCities[i].name})
         }
         //console.log(this.state.Origin,":) ---) ",this.state.Destination)
      }
      
      
   }
   async changeDate(){
      const {DateTime} = this.props;
      //console.log("dateTime: ",DateTime,". ",this.props)
      await this.setState({
         DateStart: DateTime.dateStart,
         DateEnd: DateTime.dateEnd
      })
      //console.log("this.state: in changeDate_ ",this.state)
   }


   // create service
   async sendReq(){
         
      await this.changeOrigin()
      await this.changeDate()
      const axios = require("axios")
      //console.log("state before create a service: ",this.state)
         await axios.post(URLGRAPH, { 
            query : `mutation{
               createService(Service: {
                  id_user: ${this.state.id_user}
                  title:"${this.state.Title}"
                  class:"${this.state.Type}"
                  capacity: ${this.state.Capacity}
                  places: ${this.state.Places}
                  cost: "${this.state.Cost}"
                  origin :"${this.state.Origin}"
                  destination :"${this.state.Destination}"
                  transport :"${this.state.Transport}"
                  ubication:"${this.state.Ubication}"
                  date_start:"${this.state.DateStart}"
                  date_end:"${this.state.DateEnd}"
               }){
                  id
                  title
               }
            }`
        }).then((result)=>{
            
          //console.log(result.data.data.createService)
          const title = result.data.data.createService.title
          const id = result.data.data.createService.id
          if(title !== " "){
              //console.log("service created")
              this.setState({ id: id})
          }else{
              alert("No se creo el servicio")
          }
      })
   }
   async sendUpdate(event){
      const axios = require("axios")
      await this.changeDate()
      //console.log("this is the id?: ",localStorage.getItem('id_service'))
      // Load the old data in service
      //console.log("updating service....")
      let id_service = localStorage.getItem('id_service')
      //console.log("cards in service: ",Object.keys(this.state.cards).length)
      let size = Object.keys(this.state.cards).length
      var service = [];
      for (var i = 0, max = size; i < max; i += 1) {
         var id_card = parseInt(this.state.cards[i].id)
         var service_id = parseInt(id_service)
         if (id_card === service_id){service.push(this.state.cards[i]);}
      }
      service = service[0]
      //console.log("Id_service: ",service_id)
      /*console.log("servicio before: ",service[0]["title"])
      console.log("servicio before2: ",service[0].title)
      console.log("this.state.Title: ",this.state.Title)*/
      //console.log("service modified: ",service)
      //console.log("<<<: ",this.state.Capacity ," , ", service.capacity)
      if( this.state.Title === ' '){
         await this.setState({Title: service.title})
      }
      if(this.state.Capacity === 0){
         await this.setState ({ Capacity: service.capacity})
      }
      if(this.state.Description === ' '){
         await this.setState({Description: service.descrip})
      }
      if(this.state.Places === 0){
         await this.setState({Places: service.places})
      }
      if(this.state.Cost === ' '){
         await this.setState({Cost: service.cost})
      }
      if(this.state.Origin === ' '){
         await this.setState({Origin: service.origin})
      }
      if( this.state.Destination === ' '){
         await this.setState({Destination: service.destination})
      }
      if(this.state.Transport === ' '){
         await this.setState({Transport: service.transport})
      }
      if(this.state.DateStart === ' ' || this.state.DateStart === ''){
         console.log("DateStart in blank")
         await this.setState({DateStart: service.date_start})
      }
      //console.log("DateEnd: ",this.state.DateEnd)
      if(this.state.DateEnd === ' ' || this.state.DateEnd === ''){
         await this.setState({DateEnd: service.date_end})
      }

      //console.log("state after update: ",this.state)
      //console.log("after DateEnd: ",this.state.DateEnd)
      axios.post(URLGRAPH, { 
          query : `mutation{
            updateService(id: ${id_service},Service:{
               id_user: ${this.state.id_user}
               title:"${this.state.Title}"
               class:"${this.state.Type}"
               capacity: ${this.state.Capacity}
               places: ${this.state.Places}
               cost: "${this.state.Cost}"
               origin :"${this.state.Origin}"
               destination :"${this.state.Destination}"
               transport :"${this.state.Transport}"
               ubication:"${this.state.Ubication}"
               date_start:"${this.state.DateStart}"
               date_end:"${this.state.DateEnd}"
            }){
               id
               id_user
               title
            }
         }`
      }).then((result)=>{
          
        //console.log(result.data.data.updateService)
         const id_user = result.data.data.updateService.id_user
         if(id_user !== -1){
            //console.log("Servicio Actualizado")
         }else{
            alert("No se actualizo el servicio")
         }
      })
   }

   //carga datos al entrar
   componentDidMount(){this.cargarDatos()}
   
   handleClose(){ 
      this.setState({ open: !this.state.open})
   }

    // open the list(Transport)
   handleClick() {
      this.setState({ setOpenC: !this.state.openC,openC: !this.state.setOpenC})
   };

   // Open the train
   OpenTrain(){
      this.setState({
         openT: !this.state.openT
      })
   }
   handleClickO(){
      this.setState({ setOpenO: !this.state.openO,openO: !this.state.setOpenO })
   }

   async cargarDatos(){
      //console.log("Cargando datos") 
      const axios = require("axios") 

      await this.setState({email: localStorage.getItem('email')})
      await axios.post(URLGRAPH, {
         query: `query{
            userByEmail(email:"${this.state.email}"){
               id
               nameE
            }
         }`
      
      })
      .then((result)=>{
         //console.log("request")
         let user = result.data.data.userByEmail
         this.setState({
            id_user: user.id
         })   
         //console.log("email: ", this.state.email)
      }).catch(err => console.log(err))
      
      //console.log("USER ID: ",this.state.id_user)

      await axios.post(URLGRAPH, {
         query: `query{
            getServices{
               id 
               id_user 
               title 
               class 
               capacity
               places 
               cost 
               origin 
               destination 
               transport 
               date_start
               date_end
            }
         }`
      
      })
      .then((result)=>{
         let service = result.data.data.getServices
         this.setState({
            cards: service
         })   
         //console.log("service: ", this.state.cards)
      }).catch(err => console.log(err))

   /*await axios({
      url: URLGRAPH,
      method: 'post',
      data: {"query":"query{ getServices{id id_user title class descrip}}","variables":null}
   })
   //console.log("email before the query: ",this.state.email)
      
      /*await this.setState({email: localStorage.getItem('email')})
      console.log("email before the query: ",this.state.email)
      */
      
      /*axios.get('/services',{
         method: 'GET',
         mode: 'cors',
         headers: { 'Access-Control-Allow-Origin': true },
      })
      .then((result)=>{
         let service = result.data
         this.setState({
            cards: service
         })   
         console.log("service: ", this.state.cards)
      })
      .catch(err => console.log('err', err));
      */

      // solicita el id guardado en localStorage
      
      // solicita el correo 
      
   }
   
   render() {
      
      // put in localStorage.view 
      localStorage.setItem('view', 'service');
      const { classes } = this.props;
      //console.log("cards in services: ",this.state.cards)
      let cards = this.state.cards.map(card =>{
         if (card.id_user === this.state.id_user){
            //console.log("card que voy a enviar: ",card)
            return(
               <Card 
                  key={card.id}  
                  card={card}
                  text ={this.InputChange}
                  list ={this.changeState}
                  city ={this.changeOrigin}
                  request = {this.sendUpdate}
                  onClose = {this.handleClose}
                  id ={card.id}
               />
            )
         }
         return null
      })
      return(
         <div className={classes.root}>
            <Grid container  direction="row" justify="flex-start" alignItems="flex-start">
               <Grid item xs={12}>
                  <Navbar/>
                  <CssBaseline/>        
               </Grid>
               <Grid item xs={12} sm={4} md={3}>
                  <Container>
                     <Grid container direction="column" className={classes.menu_service}>
                        <Grid item xs={12} align="center">
                           <FormControl component="fieldset" className={classes.formControl}>
                              <Fab color="secondary" className={classes.fab}>
                                 <AddIcon onClick={this.handleClose}/>
                              </Fab>
                           </FormControl>
                        </Grid>
                        
                        <DialogService 
                           open ={this.state.open} 
                           onClose ={this.handleClose}
                           request ={this.sendReq} 
                           type ={'Crear'}
                           text ={this.InputChange}
                           list ={this.changeState}
                           city ={this.changeOrigin}
                        />
                        <Grid item xs={12} align="center">
                           <Paper elevation={3}>
                              <div className={classes.paper_info}>
                                 <p>
                                    Bienvenido al portal, para crear un nuevo servicio presione
                                    <AddIcon className={classes.paper_plus} style={{ fontSize: 35 }}/>
                                 </p>
                              </div>
                           </Paper>
                           
                        </Grid>
                     </Grid>
                  </Container>
               </Grid>
               <Grid container  direction="column"xs={12} sm={8} md={6} item={true} spacing={2} justify="center" alignItems="center" >
                  
               </Grid>
               <Grid container xs={12} sm={8} md={9} item={true} spacing={2} className={classes.cards}>
                  {cards}
               </Grid>
            </Grid>
         </div>
      )
   }
}

const mapStateToProps = (state) => {
   return {
      listCities: state.ListCities,
      DateTime: state.DateTime
   };
};
export default withRouter(withStyles(styles)(connect(mapStateToProps)(Services)));