export const defaultState = {
    open: false,
    name: '',
    free: 0,
    full: 0
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE':
            return{
                ...state,
                free: payload[0],
                full: payload[0] - payload[1]
            }
        default:
            return{
                ...state,
                name: payload
            }

    }
}

export default reducer;