import React, { Component } from 'react'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid' 
import Button from '@material-ui/core/Button';
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';
import train from '../resources/Train.png';
//import { connect } from 'tls';
//import { O_TRUNC } from 'constants';

//redux
import {connect} from 'react-redux'


const styles = theme => ({
    TrainE:{
        backgroundColor: '#34eb6e',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center',
        borderRadius: '20px'
    },
    TrainF:{
        backgroundColor: '#e34242',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center',
        borderRadius: '20px'
    },
    all:{
        justifyContent: 'center',
        textAlign: 'center'
    },
    text_info:{
        paddingTop:'7px',
        fontFamily: 'Lora',
        fontSize: "20px",
    },
    TrainSE:{
        marginTop:'20px',
        backgroundColor: '#34eb6e',
        width: '40px',
        height:'40px',
        justifyContent: 'center',
        textAlign: 'center',
        borderRadius: '20px'
    },
    TrainSF:{
        marginTop:'20px',
        backgroundColor: '#e34242',
        width: '40px',
        height:'40px',
        justifyContent: 'center',
        textAlign: 'center',
        borderRadius: '20px'
    },
})

class Train extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            open: false,
            total: 180 ,
            free: 12
        }
        this.OpenTrain = this.OpenTrain.bind(this)
    }
    OpenTrain(){
        this.setState({
           open: !this.state.open
        })
        
    }

    render(){
        const { classes } = this.props;
        const { Train } = this.props
        let title = 'Estado del Tren: '+ Train.name
        return(
            
            <Dialog open={this.props.open} onClose={this.props.onClose}>
                <DialogTitle ><b>{title}</b></DialogTitle>
                <DialogContent>
                    <Grid container direction="row" item={true} spacing={1} className={classes.all}>
                        <Grid item xs={12} sm={4} md={4} align="center">
                            <img src={train} alt="train" width="150" height="110"/>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center" >
                            <div className={classes.TrainE}>
                                <h2>{Train.free}</h2>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center">
                            <div className={classes.TrainF}>
                                <h2>{Train.full}</h2>
                            </div>
                        </Grid>

                        <Grid item xs={12} sm={8} md={8} align="center" className={classes.text_info}>
                            <p>
                            Contenedor <b>Verde</b> indica
                            las plataformas <i>libres del tren</i>
                            </p>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center" >
                            <div className={classes.TrainSE}>
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={8} md={8} align="center" className={classes.text_info} >
                            <p>
                            Contenedor <b>Rojo</b> indica
                            las plataformas <i>ocupadas del tren</i>
                            </p>
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} align="center">
                            <div className={classes.TrainSF}>
                            </div>
                        </Grid>

                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.props.onClose} color="primary"> cerrar </Button>
                </DialogActions>
            </Dialog>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       Train: state.Train
    };
};
export default withRouter(withStyles(styles)(connect(mapStateToProps)(Train)));