export const defaultState = {
    isAuthenticated: false
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE':
            switch(payload){
                case true:
                    return{
                        ...state,
                        isAuthenticated: true
                    }
                default:
                    return{
                        ...state,
                        isAuthenticated: false
                    }
            }
        default:
            return state
    }
}

export default reducer;