export const defaultState = {
    dateStart: new Date('2019-10-31T09:00:00'),
    dateEnd: new Date('2019-10-31T09:00:00')
}

const reducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE':
            //console.log("change in reducers")
            switch(payload[0]){
                case 'salida':
                    return{
                        ...state,
                        dateStart: payload[1]
                    }
                default:
                    return{
                        ...state,
                        dateEnd: payload[1]
                    }
            }
            default:
                return state
    }
}

export default reducer;