import React,{Component}   from 'react';
 
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import logo from '../resources/loading-icon.gif'
import withStyles from '@material-ui/core/styles/withStyles';

const styles = theme => ({
    content:{
        textAlign: 'center',
        fontSize:'20px',
    },
    logo:{
        marginLeft:'30px'
    }
})
class DialogLoading extends Component{
    constructor(props) {
        super(props)
        this.state = {}
    }
 
    
    render(){
        const { classes } = this.props;
        return(
            
            <Dialog open={this.props.open} onClose={this.props.onClose} >
                <DialogContent>
                    <img src={logo} className={classes.logo} width="250" height="250" alt="loading..." />
                    <div className={classes.content}>
                        <h2>Registrando usuario....</h2>    
                    </div>
                </DialogContent>
           </Dialog>
        );
    }
}
 
 
export default withStyles(styles)(DialogLoading);