import React, { Component } from 'react'
import { CssBaseline, Grid} from '@material-ui/core';
import Navbar from './NavBar'
import { withRouter } from 'react-router-dom';
import withStyles from '@material-ui/core/styles/withStyles';
import Hidden from '@material-ui/core/Hidden';
import Paper from '@material-ui/core/Paper';
import MenuIcon from '@material-ui/icons/Menu'
import SearchIcon from '@material-ui/icons/Search';
// link to api
import { URLGRAPH } from '../constants'

// navbar search 
import Map from './Map'

// Redux
import {connect} from 'react-redux'

   const styles = theme => ({
      root: {
         display: 'flex',
         flexGrow: 1,
         overflow: 'hidden',
      },
      
      appBarSpacer: theme.mixins.toolbar,
      content: {
         flexGrow: 0,
         height: '100vh',
         overflow: 'auto',
      },
      container: {
         paddingTop: theme.spacing(10),
         paddingBottom: theme.spacing(2),
         height: '100vh',
      },
      columna: {
         direction: 'row',
      },
      formControl: {
         margin: theme.spacing(2),
         marginTop:'30px'
      },
      labelPublico: {
         marginBottom: '1em',
         marginTop: '2em'
      },
      // for plus
      fab: {
         position: 'relative',
         marginTop:'30px',
         bottom: theme.spacing(2),
         right: theme.spacing(2),
      },
      // for search
      
      button: {
         height: '50px',
         width: '115px'
      },
      textField: {
         width: '350px',
         textAlign: "center",
         backgroundColor: '#b5d4ff'
      },
      menu: {
         width: 200,
      },
      form:{
         paddingTop: "50px",
         textAlign: "center",
         justifyContent: "center",
         height: '200px',
         //position: "fixed"
      },
      main:{
         backgroundImage: "url(https://cdn.pixabay.com/photo/2017/02/05/04/21/keppel-2039122_960_720.jpg)",
         backgroundRepeat: 'no-repeat',
         backgroundSize: 'cover',
         height: '400px',
         fontFamily: 'Lora',
         textAlign: "center",
         color: 'white',
         justifyContent: "center",
         alignItems: "center"
      },
      main_title:{
         fontSize: "80px",
      },
      main_text:{
         fontSize: "35px",
      },
      mapa:{
         paddingLeft: '10px',
         
      },
      card:{
         paddingLeft: "150px"
      },
      other_div:{
         width: "200px",
         height: "300px",
      },
      paper:{
         width: '300px',
         //marginLeft: '60px'
      },
      paper_info:{
         width: '280px',
         height:'120px',
         fontFamily: 'Lora',
         textAlign: 'center',
         fontSize: "20px",
         
      },paper_plus:{
         marginBottom: '-12px',
         marginLeft: '20px'
      }
   
   })

class User extends Component {
   constructor(props){
      super(props);
      this.state = { 
         cards: [],
         swa: false,
         swb: false,
         swc: false,
         //origin: '',
         //destination: '',
      }
      this.handleChange = this.handleChange.bind(this);
      this.ChangeValue = this.ChangeValue.bind(this);
      this.handleInputChange = this.handleInputChange.bind(this);
      this.sendReq = this.sendReq.bind(this);
      this.sendReqB = this.sendReqB.bind(this);
   }
   

   // select the Origin value
   ChangeValue(){

   }

   // change values of switch
   handleChange(event) {
      const value = event.target.value
      if (value === "swa"){
         this.setState ({swa: !this.state.swa})
      }
      else if (value === "swb"){
         this.setState ({swb: !this.state.swb})
      }
      else if (value === "swc"){
         this.setState ({swc: !this.state.swc})
      }
   
   }
   // save the origin and destination in this.state
   async handleInputChange(event) {

      const target = event.target.name;
      const value = event.target.value;
      switch(target){
          case "origin":
              await this.setState({ origin: value})
          break;
          default:
              await this.setState({ destination: value})
          break;
      }
      
   }
   // search the all alternatives
   async sendReqB(){
      if (this.state.origin=== ''|| this.state.destination === ''){
         alert("completa los campos requeridos!")
      }else{
         this.sendReq()
      }
   }

   async sendReq(){
      const axios = require("axios")
      // get alternatives
      await axios.post(URLGRAPH, {
         query: `query{
            getAlternatives{
               id 
               cost
               array_id 
               duration
               origin
               destination
            }
         }`
      
      })
      .then((result)=>{
         let alternative = result.data.data.getAlternatives
         console.log("alter: ",alternative)
         this.setState({
            cards: alternative
         })   
         
      })
         .catch(err => console.log(err))
      /*
      
      await axios
         .all([axios.get(URLGRAPH+'/alternatives')])
         .then(
            axios.spread((alternative) => {
               const alter_data = alternative.data;
               console.log("alter: ",alter_data)
               this.setState({
                  cards: alter_data
               })   
            })
         );

      */

  }


   render(){
      
      //view for navbar 
      localStorage.setItem('view', 'user');
      //console.log("lc in user: ",localStorage.getItem('view'))
      const {classes} = this.props;
      const { listCities } = this.props
      console.log("en user esta es list: ",listCities)
      
      return(
         <div className={classes.root}>
            <Grid container direction="row" justify="flex-start" alignItems="flex-start">
                  
               <Grid item xs={12} >
                  <Navbar info={this.state.cards}/>
                  <CssBaseline/>        
               </Grid>
               
               <Grid container direction="row" item spacing={3}  >
                  <Grid item xs={12} sm={9} md={9} className={classes.mapa}>
                     <Hidden only="xs"><Map/></Hidden>
                  </Grid>
                  <Grid item xs={12} sm={3} md={3} className={classes.mapa} align="center">
                     <Paper elevation={3} className={classes.paper}>
                        <div className={classes.paper_info}>
                              <p>
                              Bienvenido al portal, para buscar un nuevo servicio presione 
                              <MenuIcon className={classes.paper_plus} />
                           </p>
                        </div>
                     </Paper>
                     <Paper elevation={3} className={classes.paper}>
                        <div className={classes.paper_info}>
                           <p>
                              A continuación encontrará un menú, ahi podrá seleccionar
                              el origen y destino
                           </p>
                        </div>
                     </Paper>
                     <Paper elevation={3} className={classes.paper}>
                        <div className={classes.paper_info}>
                           <p>
                              Finalmente para obtener las alternativas
                              debe presionar
                              <SearchIcon className={classes.paper_plus} />
                              
                           </p>
                        </div>
                     </Paper>
                  </Grid>
                  
                  <Grid container  direction="column"xs={12} sm={8} md={6} item={true} spacing={2} justify="center" alignItems="center" >
                     
                  </Grid>
               </Grid>

            </Grid>
         </div>
      )
   }
}
const mapStateToProps = (state) => {
   return {
      listCities: state.ListCities,
   };
};

export default withRouter(
   withStyles(styles)(connect(mapStateToProps)(User))
);

