import React, { Component } from 'react'
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';

// List
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import Collapse from '@material-ui/core/Collapse';
//import Train from './Train'

// Icons
import GroupIcon from '@material-ui/icons/Group';
import GolfCourseIcon from '@material-ui/icons/GolfCourse';
import LabelIcon from '@material-ui/icons/Label';


// Redux
import {connect} from 'react-redux'
//import {change} from '../redux/actions/Train'
import { Grid } from '@material-ui/core';
var wtf8 = require('wtf-8');
var unidecode = require('unidecode');


const styles = theme => ({
    TrainE:{
        backgroundColor: '#34eb6e',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center'
    },
    TrainF:{
        backgroundColor: '#e34242',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center'
   },list:{
      color: '#48bffa'
   },
   nested:{
      fontFamily: 'Lora',
      fontSize: "15px",
      
   }
})

class ListInfo extends Component {
   
   constructor(props){
      super(props);
      this.state = {
         SetOpen: false,
         open: false,
         openT: false,
         info:[
            {
               'id': 1,
               'nombre': 'Pepito Perez',
               'rol': 'Transportador',  
               'Servicio': 'otra cosa'
            },
            {
               'id': 2,
               'nombre': 'juan Perez',
               'rol': 'Transportador',  
               'Servicio': 'otra cosa x2'
            }
         ],
         capacity: 120,
         places: 10
            
         
      }
      this.handleClick = this.handleClick.bind(this)
      this.updateTrain = this.updateTrain.bind(this)
      
      
   }
   // Origin 
   handleClick(){
      localStorage.setItem('type', 'Origin');
      this.setState({
         setOpen: !this.state.open,
         open: !this.state.setOpen
      })
   }
   updateTrain(value){
      console.log("value: ",value)
      //this.props.change(value)
   }
   /*componentWillMount() {
      //this.props.change([this.state.capacity,this.state.places])   
   }*/
   
   
   render(){
      const { classes } = this.props;
      const { alternatives} = this.props;
      //const { services } = this.props;
      //const { users } = this.props;
      const { Train } = this.props;
      console.log(Train)
      
      let serv = []
      this.props.services.map(option => {
         //console.log(option.title)
         serv.push([option.id,option.title,option.date_start ,option.date_end])
         return null
      })

      //console.log(serv[0])
      //console.log(Object.entries(services[0]));
      let info = this.props.users.map(option => {
         //console.log("id card: ",alternatives.array_id.split(',')[option.id-1])
         let id_user = 0;
         let id_service = 0;
         if (alternatives.array_id.includes(',')){id_user = parseInt(alternatives.array_id.split(',')[option.id-1])}
         else{id_user = parseInt(alternatives.array_id)}

         if (alternatives.array_services.includes(',')){id_user = parseInt(alternatives.array_services.split(',')[option.id-1])}
         else{id_service = parseInt(alternatives.array_services)}
         
         //let id_service = parseInt(alternatives.array_services.split(',')[option.id-1])
         //console.log(alternatives.array_id ,"!!!",alternatives.array_services)
         //console.log("ListInfo....")
         
         let any = this.props.services.map(service =>{
            //console.log(option.id ,"<<>>", id_user ,"&", service.id ,"<<>>", id_service)
            if (option.id === id_user && service.id === id_service){
               return(
                  <List component="div" disablePadding key={option.id}>
                     <ListItem className={classes.nested}>
                        <Grid item md={2}><GroupIcon color="primary"/></Grid>
                        <Grid item md={10}>Información de Contacto:</Grid>
                     </ListItem>
                     <ListItem className={classes.nested}>
                        <Grid item md={7}>{option.nameE}</Grid>
                        <Grid item md={5}>Rol: {option.rol}</Grid>
                     </ListItem>
   
                     <ListItem className={classes.nested}>
                        <Grid item md={2}><GolfCourseIcon color="primary"/></Grid>
                        <Grid item md={10}>{unidecode(service.title)}</Grid>
                     </ListItem>

                     <ListItem className={classes.nested}>
                        <Grid item md={2}><LabelIcon color="primary"/></Grid>
                        <Grid item md={10}>{unidecode(service.origin)}</Grid>
                     </ListItem>

                     <ListItem className={classes.nested}>
                        <Grid item md={2}><LabelIcon color="primary"/></Grid>
                        <Grid item md={10}>{service.date_start.slice(0,21)}</Grid>
                     </ListItem>

                     <ListItem className={classes.nested}>
                        <Grid item md={2}><LabelIcon color="primary"/></Grid>
                        <Grid item md={10}>{unidecode(service.destination)}</Grid>
                     </ListItem>
                     
                     <ListItem className={classes.nested}>
                        <Grid item md={2}><LabelIcon color="primary"/></Grid>
                        <Grid item md={10}>{service.date_end.slice(0,21)}</Grid>
                     </ListItem>
                     
                  </List>
               )
            }
            return null
         })
         return any
      })
      //console.log("classe of list-->> ",listCities)
      //this.props.openList('hallo')
      return(
         <List justify="center" color={'black'}>
            <ListItem button onClick={this.handleClick}>
               <ListItemText primary={this.props.type}/>
               {this.state.open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={this.state.open} timeout="auto" unmountOnExit>
               <List component="div" disablePadding >
                  {info}
               </List>
            </Collapse>
            
         </List>
         
      )
   }
}

const mapStateToProps = (state) => {
   return {
      listCities: state.ListCities,
      Train: state.Train
   };
};
const mapDispatchToProps = (dispatch) => {
   return{
      choose: (text) => {dispatch({type: 'CHOOSE' , text: text})},
      // change: (option) => {dispatch(change(option))},
   }
}

export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(ListInfo)));