import React, { Component } from 'react'
//import {GoogleMap} from 'react-google-maps'
import { Map, GoogleApiWrapper ,Marker} from 'google-maps-react';
// Redux
import {connect} from 'react-redux'

const mapStyles = {
    width: '76%',
    height: '90%',
    position: 'relative'
};


//let LocalS = localStorage.getItem('route')
class Mapa extends Component {
    constructor(props) {
        super(props)
        this.state = {
            
        }
    }
    
    
    render() {
        //const { classes } = this.props;
        const { listCities } = this.props
        console.log("en map esta es list: ",listCities)
        
        /*
        
        for (let i= 0; i < 2; i++){
            console.log("in the for: ",listCities[i].option,i)
                if(listCities[i].option === true){
                    let ubication = { lat: listCities[i].lat, lng: listCities[i].lng}
                    console.log("ubication: ",listCities[i].lat,listCities[i].lng)
                    let markers = ({
                        return(
                            <Marker id={listCities[i].id} position={ubication}/>
                        )
                    })
                }
        }*/
        let Cities = [];
        var len = Object.keys(listCities).length
        for (let i= 0; i < len; i++){Cities[i] = listCities[i]}
        
        
        let markers = Cities.map(City=>{
            if(City.option === true){
                let ubication = { lat: City.lat, lng: City.lng}
                console.log("ubication: ",City.lat,City.lng)
                return(
                    <Marker key={City.id} position={ubication}/>
                )
            }else{
                return('')
            }
        })

        return (
            
            <Map
                google={this.props.google}
                zoom={6}
                style={mapStyles}
                initialCenter={{ lat: 4.6097100, lng: -74.0817500}}
            >   
                {markers}
                
            </Map>
            
            
        );
    }
}

const mapStateToProps = (state) => {
    return {
       listCities: state.ListCities,
    };
 };
 

export default GoogleApiWrapper({
    apiKey: 'AIzaSyDnn95mzyotY9Iy2hafYGXckSSh-Bk7JLw'
})(connect(mapStateToProps)(Mapa));