import React, { Component } from 'react'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContentText from '@material-ui/core/DialogContentText';
import Button from '@material-ui/core/Button';
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Radio from '@material-ui/core/Radio';
import { RadioGroup } from '@material-ui/core';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import Collapse from '@material-ui/core/Collapse';
import { Grid } from '@material-ui/core';


//ListCities
import ListCities from './ListCities'
import DateTime from './DateTime'

//icons
import Fab from '@material-ui/core/Fab';
import SaveIcon from '@material-ui/icons/Save';
import AttachFileIcon from '@material-ui/icons/AttachFile';

//Icon info
import Popover from '@material-ui/core/Popover';
import Typography from '@material-ui/core/Typography';

const styles = theme => ({
    TrainE:{
        backgroundColor: '#34eb6e',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center'
    },
    TrainF:{
        backgroundColor: '#e34242',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center'
    },
    text_info:{
      fontFamily: 'Lora',
      fontSize: "15px",
      justifyContent: 'center',
      marginTop: '20px',
      paddingLeft: '5px',
   },//popOver
   typography: {
      padding: theme.spacing(2),
      paddingTop: '10px'
   },
})

class DialogService extends Component {
    
   constructor(props){
      super(props);
      this.state = {
         SetOpenO: false,
         openO: false,
         openD: false,
         setOpenD: false,
         openT: false,
         setOpenT: false,
         //popOver
         openP: false,
         anchorEl: null

      }
      
      this.handleClick = this.handleClick.bind(this)
      this.handleClose = this.handleClose.bind(this)
      
   }
   //Transport
   handleClick() {
      this.setState({
         setOpenT: !this.state.openT,
         openT: !this.state.setOpenT
      })
   };

   //PopOver
   handleClose(event){
      this.setState({
         anchorEl: event.currentTarget,
         openP: !this.state.openP
      })
   }

   render(){
        const { classes } = this.props;
        //localStorage.setItem('id_service',this.props.id)

        const id = this.state.open ? 'simple-popover' : undefined;
        return(
            <Dialog open={this.props.open} onClose={this.props.onClose} >
                <DialogTitle id="alert-dialog-title">{"Crear Nuevo servicio"}</DialogTitle>
                <DialogContent>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="title"
                        label="Titulo"
                        autoFocus
                        onChange={this.props.text}
                    />
                    <List justify="center">
                        <ListItem button onClick={this.handleClick} >
                            <ListItemText primary="Modo" />
                            {this.state.openT ? <ExpandLess/> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={this.state.openT} timeout="auto" unmountOnExit>
                            <List component="div" disablePadding>
                                <RadioGroup>
                                    <ListItem className={classes.nested}>
                                        <FormControlLabel control={ <Radio onChange={this.props.list} value="terrestre"/>}
                                        label="Terrestre"/>
                                    </ListItem>
                                    <ListItem className={classes.nested}>
                                        <FormControlLabel control={ <Radio onChange={this.props.list} value="ferreo"/>}
                                        label="Ferreo"/>
                                    </ListItem>    
                                    <ListItem className={classes.nested}>
                                        <FormControlLabel control={ <Radio onChange={this.props.list} value="fluvial"/>}
                                        label="Fluvial"/>
                                    </ListItem>
                                    <ListItem className={classes.nested}>
                                        <FormControlLabel control={<Radio onChange={this.props.list} value="aereo"/>}
                                        label="Aereo"/>
                                    </ListItem>
                                </RadioGroup>
                            </List>
                        </Collapse>
                    </List>
                    <Grid container direction={'row'}>
                        <DateTime labelDate={"Fecha Salida"} labelTime={"Hora Salida"}/>
                        <DateTime labelDate={"Fecha Llegada"} labelTime={"Hora Llegada"}/>
                    </Grid>
                    <Grid container direction={'row'}>
                        <Grid item xs={10}>
                            <TextField
                                variant="outlined"
                                margin="normal"
                                disabled={false}
                                fullWidth
                                id="capacity"
                                label="Capacidad"
                                autoFocus
                                onChange={this.props.text}
                            />
                        </Grid>
                        <Grid item xs={2} align={'center'} className={classes.text_info}>
                            <Fab color="secondary" size={'small'} className={classes.fab}>
                                <AttachFileIcon onClick={this.handleClose}/>
                                <Popover
                                    id={id}
                                    open={this.state.openP}
                                    anchorEl={this.state.anchorEl}
                                    onClose={this.handleClose}
                                    anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'center',
                                    }}
                                    transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'center',
                                    }}
                                >
                                    <Typography className={classes.typography}>
                                    <b>Capacidad: </b>Plataformas que posee el tren 
                                    </Typography>
                                    <Typography className={classes.typography}>
                                    <b>Espacios: </b>Plataformas disponibles del tren
                                    </Typography>
                                </Popover>
                            </Fab>
                        </Grid>
                    </Grid>
                     <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="places"
                        label="Espacios"
                        autoFocus
                        onChange={this.props.text}
                     />

                    <ListCities type="Origen"/>
                    <ListCities type="Destino"/>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        //required
                        fullWidth
                        id="costo"
                        label="Costo"
                        name="costo"
                        autoFocus
                        onChange={this.props.text}
                    />
                    <DialogContentText id="alert-dialog-description">
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Fab color="secondary" size={'small'} className={classes.fab}>
                           <SaveIcon onClick={this.props.request}/>
                        </Fab>
                        <Button onClick={this.props.onClose} color="primary" > Cerrar </Button>
                     </DialogActions>
               </Dialog>
               
            
        )
    }
}
export default withRouter(withStyles(styles)(DialogService));