import React, { Component } from 'react'
import { withStyles } from '@material-ui/styles';
import { Link,withRouter } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'


import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import AccountCircle from '@material-ui/icons/AccountCircle';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Badge from '@material-ui/core/Badge';
import MailIcon from '@material-ui/icons/Mail';

import MoreIcon from '@material-ui/icons/MoreVert';
import Button from '@material-ui/core/Button';


const styles = theme => ({
   
   grow: {
      flexGrow: 1,
   },
   menuButton: {
      marginRight: theme.spacing(2)
   },
   title: {
      display: 'none',
      color: '#0398fc',
      [theme.breakpoints.up('sm')]: 
      {
         display: 'block',
      },
   },
   
   ad:{
      color: 'blue',
      justifyContent: 'center',
      alignItems: 'center',
      paddingLeft: '200px'
   },
    
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 6),
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: 200,
        },
    },
      sectionDesktop: {
         display: 'none',
         textDecoration: 'none',
         alignItems: 'center',
         [theme.breakpoints.up('md')]: {
               display: 'flex',
         },
         
      },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
   textoButton: {
      color: 'white',
      textDecoration: 'none',
   },

});
class NavBar extends Component{
   constructor(props){
      super(props);
      this.state = {
         open: false,
         anchorEl: null,
         mobileAnchorEl: null,
         MobileOpen: false,
         isMobileOpen: false,
         MenuOpen: false,
         SetMenuOpen: false,
         
      }
      this.handleProfileMenu = this.handleProfileMenu.bind(this);
      this.handleMobileMenu = this.handleMobileMenu.bind(this);
      this.LinkProfile  = this.LinkProfile.bind(this);
      this.LinkLogout = this.LinkLogout.bind(this);
   }
   
   
    // Open Mobile menu
   handleMobileMenu(event) {
      this.setState({
         mobileAnchorEl: event.currentTarget,   
         MobileOpen: !this.state.isMobileOpen,
         isMobileOpen: !this.state.MobileOpen
      })
   }

   // Open profile menu
   // anchor is the element used to set the position of the menu 
   handleProfileMenu(event) {
      this.setState({
         anchorEl: event.currentTarget,   
         MenuOpen: !this.state.SetMenuOpen,
         SetMenuOpen: !this.state.MenuOpen
      })
   }
   LinkProfile(event){
      this.setState({
         MenuOpen: !this.state.SetMenuOpen,
         SetMenuOpen: !this.state.MenuOpen
      })
   }
   LinkLogout(){
      console.log("logout")
      this.setState({
         MenuOpen: !this.state.SetMenuOpen,
         SetMenuOpen: !this.state.MenuOpen
      })
      this.props.history.push("/")
   }
    
   render(){
      const menuId = 'primary-search-account-menu';
      const renderMenu = (
         <Menu
            anchorEl={this.state.anchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={menuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={this.state.MenuOpen}
            onClose={this.handleMenuClose}>
            <MenuItem onClick={this.LinkProfile}>Perfil</MenuItem>
            <MenuItem onClick={this.LinkLogout}>Cerrar Sesión</MenuItem>
         </Menu>
      );
      const mobileMenuId = 'primary-search-account-menu-mobile';
      const renderMobileMenu = (
         <Menu
            anchorEl={this.state.mobileAnchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={mobileMenuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={this.state.MobileOpen}
            onClose={this.handleMobileMenuClose}
            >
            <MenuItem onClick={this.handleMobileMenu}>
               <IconButton aria-label="show 1 new mails" color="inherit">
                  <Badge badgeContent={1} color="secondary">
                     <MailIcon />
                  </Badge>
               </IconButton>
               <p>Messages</p>
            </MenuItem>
            <MenuItem onClick={this.handleMobileMenu}>
               <IconButton aria-label="show 11 new notifications" color="inherit">
                  <Badge badgeContent={2} color="secondary">
                     <NotificationsIcon />
                  </Badge>
               </IconButton>
               <p>Notificaciones</p>
            </MenuItem>

            <MenuItem onClick={this.handleMobileMenu}>
               <IconButton
                  aria-label="account of current user"
                  aria-controls="primary-search-account-menu"
                  aria-haspopup="true"
                  color="inherit"
               >
                  <AccountCircle />
               </IconButton>
               <p>Profile</p>
            </MenuItem>
         </Menu>
      );
      const { classes } = this.props;
      return(
         <div className = {classes.grow}>
            <AppBar 
               position="static"
               color = 'primary'
               >
               <Toolbar>
                  <Typography className={classes.titleI} variant="h4" noWrap>
                     IMA
                  </Typography>
                  <div className={classes.grow}/>
                     
                     <div className={classes.sectionDesktop}>
                        <Link className={classes.textoButton} to="/Login">
                           <Button
                              fullWidth
                              color="inherit"
                              className={classes.submit}
                              onClick={this.sendReq}>
                              Ingreso
                           </Button>
                        </Link>
                        <Link className={classes.textoButton} to="/Register">
                           <Button
                              fullWidth
                              variant="contained"
                              color="secondary"
                              className={classes.submit}
                              onClick={this.sendReq}>
                              Registro
                           </Button>
                        </Link>
                     </div>
                     <div className={classes.sectionMobile}>
                        <IconButton
                        aria-label="show more"
                        aria-controls={mobileMenuId}
                        aria-haspopup="true"
                        onClick={this.handleMobileMenu}
                        color="inherit"
                        >
                           <MoreIcon />
                        </IconButton>
                        
                     </div>
                     
                  </Toolbar>
               
            </AppBar>
            
            {renderMobileMenu}
            {renderMenu}
         </div>
         
      )
   }
}
export default withRouter(withStyles(styles)(NavBar))