import React, { Component } from 'react'
import { withStyles } from '@material-ui/styles';
import { withRouter } from 'react-router-dom';

import { Grid } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';

import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Divider from '@material-ui/core/Divider';

// Drawer Icons
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ImportExportIcon from '@material-ui/icons/ImportExport';
import TrainIcon from '@material-ui/icons/Train';
import Fab from '@material-ui/core/Fab';
import DirectionsBoatIcon from '@material-ui/icons/DirectionsBoat';
import AirplanemodeActiveIcon from '@material-ui/icons/AirplanemodeActive';
import DirectionsBusIcon from '@material-ui/icons/DirectionsBus';

import SearchIcon from '@material-ui/icons/Search';
import ListCities from './ListCities'
import ListInfo from './ListInfo';
import Train from './Train'
// Redux
import {connect} from 'react-redux'
import {change} from '../redux/actions/Train'
// constants 
import {URLGRAPH} from '../constants'
const drawerWidth = 460;



//var unidecode = require('wtf-8');
//var unidecode = require('unidecode');
var unidecode = require('unidecode-plus');

const styles = theme => ({
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        ...theme.mixins.toolbar,
        justifyContent: 'center',
        backgroundColor: "#2865f7",
    },
     drawerH:{
        alignItems: 'center',
        paddingBottom: '40px',
        justifyContent: 'center',
        backgroundColor: "#2865f7",
     },button:{
        color: 'white',
     }
})
class Complement extends Component{
    constructor(props){
        super(props);
        this.state = {
            open: false,
            openT: false,
            anchorEl: null,
            mobileAnchorEl: null,
            MobileOpen: false,
            isMobileOpen: false,
            MenuOpen: false,
            SetMenuOpen: false,
            //open drawer
            openD: false,
            SetOpenD: false,
            // data in drawer
            origin: '',
            destination: '',
            cards: [],
            // data for the list info
            users:[],
            services:[],
            free: 0,
            full: 0,

        }
        // query for alternatives 
        this.sendReq =  this.sendReq.bind(this);
        this.getData =  this.getData.bind(this);
        this.collectCities = this.collectCities.bind(this);
        this.handleClose = this.handleClose.bind(this);
        
        
    }
    async getData(){
        const axios = require("axios")
        
        await axios.post(URLGRAPH, {
            query: `query{ getUsers{ id nameE email rol }}`
        })
        .then((result)=>{

            let user = result.data.data.getUsers
            this.setState({ users: user})   

        }).catch(err => console.log(err))
        
        // obtain the services
        await axios.post(URLGRAPH, {
            query: `query{
                getServices{
                    id
                    id_user
                    title
                    class
                    capacity
                    places
                    cost
                    origin
                    destination
                    transport
                    date_start
                    date_end
                }
            }`
         
        })
        .then((result)=>{
            let service = result.data.data.getServices
            //console.log("user: ",user)
            this.setState({
               services: service
            })   
        }).catch(err => console.log(err))   
        
        /*
        await axios
            .all([axios.get(URLGRAPH+'/users')])
            .then(
                axios.spread((user) => {
                    const users = user.data;
                    //console.log("alter: ",alter_data)
                    this.setState({
                        users: users
                    })   
                })
            );
        
        await axios
            .all([axios.get(URLGRAPH+'/services')])
            .then(
                axios.spread((service) => {
                    const services = service.data;
                    //console.log("alter: ",alter_data)
                    this.setState({
                        services: services
                    })   
                })
            );
        
        
        */
    }
    async collectCities(){
        const {listCities} = this.props;
        console.log(listCities)
        var t = 0
        var entry = false
        var len = Object.keys(listCities).length
        for(var i = 0 ;i < len;i++){
            console.log(listCities[i].name,i)
            if (listCities[i].option === true & entry === false){
                t = 1
                entry = true
                console.log("o: ",listCities[i].name)
                await this.setState({origin: listCities[i].name})
            }
            if (listCities[i].option === true & i !== t){
                t = i
                console.log("d: ",listCities[i].name)
                await  this.setState({destination: listCities[i].name})
            }
        }
        console.log("collect: ",this.state.origin,this.state.destination)
        this.sendReq()
    }
    async sendReq(){
        console.log("saveAlternative...")
        console.log(this.state.origin,"+.+",this.state.destination)
        const axios = require("axios")
        await axios.post(URLGRAPH, {
            query: `mutation{saveAlternative(Route:{ origin: "${this.state.origin}" destination: "${this.state.destination}"})
            {id cost array_id array_services}}`     
        })
        .then((result)=>{
            console.log("result:", result.data.data.saveAlternative)
            let result_query = result.data.data.saveAlternative
            if (result_query.id === -1){
                alert("No hay alternativas disponibles")
            }
            else{
                axios.post(URLGRAPH, {
                    query: `query{ getAlternatives{ id cost array_id array_services duration origin destination }}`
                    })
                    .then((result)=>{
                        let alternative = result.data.data.getAlternatives
                        console.log("alter: ",alternative)
                        this.setState({ cards: alternative })   
                    }).catch(err => console.log(err))
            }
            
        }).catch(err => console.log(err))
        this.getData()
    }

    handleClose(){ 
        this.setState({ openT: !this.state.openT})
        let service = this.state.services[1]
        this.props.change([service.capacity,service.places])
        //this.props.changeName(this.props.card.title)
    }
    

    render(){
        const { classes } = this.props;
        //console.log("complementD")
        /*let card2 = this.state.cards.map(card =>{
            let array_id = card.array_id.split(',')
            let size = array_id.length
            let info = []
            for(var i= 0 ; i < size;i= i+1 ){
                let id = parseInt(array_id[i])
            }
            
        })
        let lists = this.state.users.map(user =>{
            return(
                <ListInfo type="Detalles" user={user} city={this.props.city}/>
            )
        })*/
        const decoder = new TextDecoder();
        let cards = this.state.cards.map(card =>{
            //if( this.state.origin === decoder.decode(card.origin) && this.state.destination === wtf8.decode(card.destination) ){
            
            let card_origin = unidecode(card.origin)
            if (card_origin === "BogotA!"){card_origin = "Bogota"}
            console.log(unidecode(card_origin) +' decode[after] '+unidecode(card.destination))
            console.log(unidecode(this.state.origin) +' decode2 '+unidecode(this.state.destination))
            if( unidecode(this.state.origin) === card_origin && unidecode(this.state.destination) === unidecode(card.destination) ){
                //if( this.state.origin === card.origin && this.state.destination === card.destination ){
                return(
                    <Card key={card.id}>
                         <CardActionArea >       
                         </CardActionArea>
                         <CardContent>
                             <Grid container direction="row" justify="flex-start" alignItems="flex-start">
                                <Grid item md={3} align="center">
                                    <Fab color="secondary" size={'small'} className={classes.fab}>
                                        <TrainIcon onClick={this.handleClose} />
                                        <Train open={this.state.openT} onClose={this.handleClose}/>
                                    </Fab> 
                                </Grid>
                                 <Grid item md={6} align="left">
                                     <Typography gutterBottom color="black" component="h2" align="left">
                                            {card_origin} - {card.destination}
                                     </Typography>
                                 </Grid>
                                 <Grid item md={3} align="right">
                                     <Typography gutterBottom  color="secondary" component="h2">
                                         15h 30 min
                                     </Typography>
                                 </Grid>
     
                                 <Grid item md={8} align="center">
                                     <p>Información detallada del servicio, asi como
                                        los encargados del mismo.
                                     </p>
                                 </Grid>
                                 <Grid item md={4} align="right">{card.duration}</Grid>
                                 <Grid item md={12}>
                                    <ListInfo type="Detalles" id={card.id} users={this.state.users} alternatives={card} services={this.state.services}/>
                                 </Grid>
                             </Grid>
                         </CardContent>
                    </Card>
                )
            }
            return null
        })
        
        return(
            <div>
                <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                    <Grid item  md={3} className={classes.drawerHeader}>
                        <IconButton className={classes.button} onClick={this.handleDrawerOpen}>
                            <TrainIcon />
                        </IconButton>
                    </Grid>
                    
                    <Grid item md={3} className={classes.drawerHeader}>
                        <IconButton className={classes.button} onClick={this.handleDrawerOpen}>
                            <DirectionsBoatIcon/>
                        </IconButton>
                    </Grid>
                    
                    <Grid item md={3} className={classes.drawerHeader}>
                        <IconButton className={classes.button} onClick={this.handleDrawerOpen}>
                            <AirplanemodeActiveIcon/>
                        </IconButton>
                    </Grid>
                    
                    <Grid item md={3} className={classes.drawerHeader}>
                        <IconButton className={classes.button} onClick={this.handleDrawerOpen}>
                            <DirectionsBusIcon/>
                        </IconButton>
                    </Grid>
                </Grid>
            
                <Grid container justify="center" alignItems="center" className={classes.drawerH}>
                    <Grid item md={1}></Grid>
                    <Grid item md={9} >
                        <ListCities type="Origen"/>
                    </Grid>
                    <Grid item md={2}></Grid>

                    <Grid item md={1}></Grid>
                    <Grid item md={9} >
                        <ListCities type="Destino"/>
                    </Grid>
                    <Grid item md={2}>
                        <IconButton className={classes.button} onClick={this.collectCities}>
                            <SearchIcon style={{fontSize: '30px'}}/>
                        </IconButton>
                    </Grid>

                    <Grid item md={10}></Grid>
                    <Grid item md={2}>
                    <IconButton className={classes.button} onClick={this.props.close}>
                        <ChevronLeftIcon style={{fontSize: '30px'}}/> 
                    </IconButton>
                    </Grid>
                </Grid>
                <Divider/>
                <Grid item md={12}>
                    {cards}
                </Grid>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return {
       listCities: state.ListCities,
    };
};
const mapDispatchToProps = (dispatch) => {
    return{
       choose: (text) => {dispatch({type: 'CHOOSE' , text: text})},
       change: (option) => {dispatch(change(option))},
    }
}

export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(Complement)))