import React, { Component } from 'react';


import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';
import Recaptcha from 'react-google-invisible-recaptcha';
//Dialog
import DialogLoading from './DialogLoading'

// list 
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
//import InboxIcon from '@material-ui/icons/MoveToInbox';
import PersonIcon from '@material-ui/icons/Person';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import List from '@material-ui/core/List';
//checkbox
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import { RadioGroup } from '@material-ui/core';

// conection with API
import {URLGRAPH} from '../constants'

const styles = theme => ({
    root: {
      height: '100vh',
    },
    image: {
      backgroundImage: 'url(https://i.imgur.com/xmKwOXk.jpg)',
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      backgroundPosition: 'center', 
    },
    paper: {
      margin: theme.spacing(8, 4),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.primary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
      textDecorationLine: 'none',
    },
    textoButton: {
      color: 'white',
      textDecorationLine: 'none',
    },
    spaceDate: {
      marginTop: theme.spacing(2)
    },
  
  })

class Register extends Component{

    constructor(props){
        super(props);
        this.state = {
            nombre: ' ',
            id: ' ',
            correo: ' ',
            password: ' ',
            rol: ' ',
            open: false,
            setOpen: false,
            nit: '',
            // para colocar los roles
            dense: {
                one: false,
                two: false,
                three: false
            },
            openD: false,
            // for captcha
            messageSent: false
        }
        // show the roles
        this.handleClick = this.handleClick.bind(this);
        // Change the role
        this.changeState = this.changeState.bind(this);
        // Obtain data from InputText
        this.handleInputChange = this.handleInputChange.bind(this);
        // send request to API
        this.sendReq = this.sendReq.bind(this);
        this.viewRequest = this.viewRequest.bind(this);
        this.verifyEmail = this.verifyEmail.bind(this);
    }
    // Show the rol
    handleClick() {
        this.setState({
            setOpen: !this.state.open,
            open: !this.state.setOpen
        })
    };

    // updatae this.state
    async handleInputChange(event) {
        
        const target = event.target.id;
        const value = event.target.value;
        var md5 = require('md5');
        switch(target){
            case "nombre":
                await this.setState({ nombre: value})
            break;
            case "email":
                await this.setState({ correo: value})
            break;
            case "password":
                await this.setState({ password: md5(value)})
            break;
            default:
                await this.setState({ nit: value})
            break;
        }
        
    }
    // Change rol in this.state
    async changeState(event){
        const rol = event.target.value
        console.log(rol)

        if (rol === "generador"){
            await this.setState({
                dense:{
                    one: !this.state.dense.one,
                    two: this.state.dense.two,
                    three: this.state.dense.three
                },
                rol: rol
            })
        }else if (rol === "transportador"){
            await this.setState({
                dense:{
                    one: this.state.dense.one,
                    two: !this.state.dense.two,
                    three: this.state.dense.three
                },
                rol: rol
            })
        }else if (rol === "operador"){
            this.setState({
                dense:{
                    one: this.state.dense.one,
                    two: this.state.dense.two,
                    three: !this.state.dense.three
                },
                rol: rol
            })
        }
        console.log(this.state.rol)
    }

    // send request to Api
    async sendReq(){
        
        const axios = require("axios")
        console.log("data!!!: ",this.state)
        
        // registrar el usuario
        await axios.post(URLGRAPH, { 
            query : `mutation{
                createUser(user: {
                    nameE:"${this.state.nombre}"
                    email:"${this.state.correo}"
                    passw:"${this.state.password}"
                    rol:"${this.state.rol}"
                    nit:"${this.state.nit}"
                }){
                    nameE
                }
            }`
        }).then((result) => {
            //const nameE = result.data.data.createUser
            const nameE = result
            console.log("nameE",nameE.data)
            console.log("nameE",nameE.nameE)
            //console.log("this.state.messageSent: ",this.state.messageSent)
            if (nameE !== " "){this.props.history.push("/")}
            else{alert("Falló el Registro")}
            //nameE !== " " & this.state.messageSent ? this.props.history.push("/") : alert("Falló el Registro")

        }).catch(err => console.log(err))
    }
    // view loading
    viewRequest(){
        //this.recaptcha.execute();
        this.setState({openD: !this.state.openD})
        this.sendReq()
    }

    verifyEmail(){
        
        const axios = require("axios")
        if (this.state.nombre === ' ' || this.state.correo === ' ' || this.state.password === ' ' || this.state.rol === ' '){
            alert(" No deben quedar campos en blanco")
        }else{
            axios.post(URLGRAPH, {
                query: `query{
                    userByEmail(email:"${this.state.correo}"){id}
                }`
            })
             .then((result)=>{
                let user = result.data.data.userByEmail
                console.log("user:",user)
                if (user.id === -1 ){this.viewRequest()}else{alert("el correo ya está en uso")}
    
            }).catch(err => console.log(err))
        }
    }

    changeView(){this.props.history.push("/login")}

    onResolved = () => {
        this.setState({messageSent: true})
        console.log(this.state);
    }

    render(){
        const { classes } = this.props;
        return(
            <Grid container component="main" className={classes.root}>
                <CssBaseline />
                <Grid item xs={false} sm={7} md={7} className={classes.image}/>
                <Grid item xs={12} sm={5} md={5} component={Paper} elevation={7} square>
                    <div className={classes.paper}>
                        <Avatar className={classes.avatar}>
                            <PersonAddIcon />
                        </Avatar>
                        <Typography component="h1" variant="h5">
                            Registro
                        </Typography>
                        <form className={classes.form} noValidate>
                            <Grid container item spacing={2} justify="center">
                                <Grid item xs={12} sm={10} >
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        id="nombre"
                                        required
                                        fullWidth
                                        label="Nombre Empresa"
                                        onChange={this.handleInputChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={10} >
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        id="email"
                                        required
                                        fullWidth
                                        label="Correo"
                                        onChange={this.handleInputChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={10}>
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        id="password"
                                        required
                                        fullWidth
                                        label="Contraseña"
                                        type="password"
                                        onChange={this.handleInputChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={10}>
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        id="nit"
                                        fullWidth
                                        label="Nit"
                                        onChange={this.handleInputChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={10}>
                                    <List justify="center">
                                        <ListItem button onClick={this.handleClick}>
                                        <ListItemIcon>
                                            <PersonIcon/>
                                        </ListItemIcon>
                                        <ListItemText primary="Rol" />
                                        {this.state.open ? <ExpandLess /> : <ExpandMore />}
                                        </ListItem>
                                        <Collapse in={this.state.open} timeout="auto" unmountOnExit>
                                        <List component="div" disablePadding>
                                            <RadioGroup >
                                                <ListItem className={classes.nested}>
                                                    <FormControlLabel
                                                    control={<Radio onChange={this.changeState} value="generador"/>}
                                                    label="Generador de Carga"
                                                    />
                                                </ListItem>
                                                <ListItem className={classes.nested}>
                                                    <FormControlLabel
                                                    control={<Radio disabled={true} onChange={this.changeState} value="transportador"/>}
                                                    label="Transportador de Carga"
                                                    />
                                                </ListItem>
                                                <ListItem className={classes.nested}>
                                                    <FormControlLabel
                                                    control={<Radio disabled={true} onChange={this.changeState} value="operador"/>}
                                                    label="Operador logistico"
                                                    />
                                                </ListItem>
                                            </RadioGroup>
                                        </List>
                                        </Collapse>
                                    </List>
                                </Grid>
                                
                                <Grid item xs={10} sm={4} align="center">
                                    <Button
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        className={classes.submit}
                                        onClick={this.sendReq}>
                                        Registrarse
                                    </Button>
                                    <DialogLoading open={this.state.openD}></DialogLoading>
                                </Grid>
                                <Grid item xs={10} sm={6} align="center">
                                    <Button
                                        fullWidth
                                        variant="outlined"
                                        color="secondary"
                                        className={classes.submit}
                                        onClick={this.changeView.bind(this)}>
                                        prefiero iniciar sesión
                                    </Button>
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                      
                                </Grid>
                           </Grid>
                        </form>
                    </div>
                </Grid>
            </Grid>
        )
    }
}
export default withRouter(withStyles(styles)(Register))  