import React, { Component } from 'react'
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';

//import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid' 
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
// body Card
import PlaceIcon from '@material-ui/icons/Place';
import ScheduleIcon from '@material-ui/icons/Schedule';

// Train
import Fab from '@material-ui/core/Fab';
import TrainIcon from '@material-ui/icons/Train';
import Train from './Train'

// Edit service
import DialogService from './DialogService'

// Redux
import {connect} from 'react-redux'
import {change,changeName} from '../redux/actions/Train'
//import {changeName} from '../redux/actions/Train'

var wtf8 = require('wtf-8');

const styles = theme => ({
   avatar:{
      backgroundColor: '#f24949',
   },
   media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
      justifyContent: 'center',
      textAlign: 'center',
   },
   texto: {
      marginTop: "1em",
   },
      descripcion: {
      marginTop: "1em",
      marginBottom: "1em"
   },
   body:{
      //justifyContent: 'center',
      //textAlign: 'center',
      //width: '140px',
      //height:'80px',
      fontFamily: 'Lora',
      fontSize: "16px",
   }
})

class MediaCard extends Component {
    
    constructor(props){
      super(props);  
      this.state = {
         dialogVerMas: false,
         dialogReservar: false,
         openT: false,
         open: false,
         id: 0
      }
      this.OpenTrain = this.OpenTrain.bind(this);
      this.handleClose = this.handleClose.bind(this);
        
    }

    handleCloseVerMas (){
        this.setState({
          dialogVerMas: false,
        })
    }
    handleCloseReservar (){
        this.setState({
            dialogReservar: false,
        })
    }
   
    // open the Train
   OpenTrain(){
      // open the train will be replaced by redux
      this.setState({
         openT: !this.state.openT
      })
      console.log("id card in openTrain: ",this.props.card.id)
      this.props.change([this.props.card.capacity,this.props.card.places])
      this.props.changeName(this.props.card.title)

   }
   //edit Service
   handleClose(){
      localStorage.setItem('id_service',this.props.card.id)
      this.setState({
         open: !this.state.open,
         id: this.props.card.id   
      })
      
   }
   
   render(){
      const { classes } = this.props;
      //const { Train } = this.props;
      //console.log("Train in CardServices: ",Train)
      
      return(
         <Grid item xs={12} sm={12} md={6}>
            <Card>
               <CardHeader
                  avatar={
                     <Avatar aria-label="recipe" className={classes.avatar}>R</Avatar>
                  }
                  //title= {wtf8.decode(this.props.card.title)}
                  title= {this.props.card.title}
                  subheader= {this.props.card.class}

                  action={
                     <IconButton aria-label="settings">
                       <MoreVertIcon />
                     </IconButton>
                  } 
               />
               <CardMedia
                  className={classes.media}
                  image="https://cdn.pixabay.com/photo/2017/02/05/04/21/keppel-2039122_960_720.jpg"
                  title="InterModalidApp"
               />
               
               <CardContent>
                  <Grid container direction="row" className={classes.body} >
                     <Grid item xs={12} md={4} align="left"><PlaceIcon color={'secondary'}/> {this.props.card.origin}</Grid>
                     <Grid item xs={12} md={8} align="right"><ScheduleIcon color={'secondary'}/> {this.props.card.date_start.slice(0,24)}</Grid>
                     <Grid item xs={12} md={4} align="left"><PlaceIcon color={'secondary'}/> {wtf8.decode(this.props.card.destination)}</Grid>
                     <Grid item xs={12} md={8} align="right"><ScheduleIcon color={'secondary'}/> {this.props.card.date_end.slice(0,24)}</Grid>
                  </Grid>
               </CardContent>
               <CardActionArea>       
                  
               </CardActionArea>
               <CardActions>
                  
                  <Grid item xs={2} align="center">
                     <Fab color="secondary" size={'small'} className={classes.fab}> <EditIcon onClick={this.handleClose}/> </Fab>
                  </Grid>
                  <Grid item xs={2} align="center">
                     <Fab color="secondary" size={'small'} className={classes.fab}>
                        <TrainIcon onClick={this.OpenTrain}/>
                     </Fab>
                  </Grid>
                  <Grid item xs={6} align="center">
                     
                  </Grid>
                  <Grid item xs={2} align="center">
                     <Fab color="secondary" size={'small'} className={classes.fab}>
                        <DeleteIcon  onClick={this.handleClickButton2}/>
                     </Fab>
                     
                  </Grid>
               </CardActions>
            </Card>
            <Train open={this.state.openT} onClose={this.OpenTrain} />
            <DialogService 
               open ={this.state.open} 
               onClose ={this.handleClose} 
               request ={this.props.request} 
               type ={'Guardar'}
               text ={this.props.text}
               list ={this.props.list}
               city ={this.props.city}
               id ={this.props.id}
               card ={this.props.card}
            />
         </Grid>
      )
   }
}
const mapStateToProps = (state) => {
   return {
      Train: state.Train
   };
};
const mapDispatchToProps = (dispatch) => {
   return{
      change: (option) => {dispatch(change(option))},
      changeName: (option) => {dispatch(changeName(option))}
   }
}
export default withRouter(withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(MediaCard)));