import React, { Component } from 'react';
import withStyles from '@material-ui/core/styles/withStyles';
import {withRouter } from 'react-router-dom';

// Material UI
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
//import Recaptcha from 'react-recaptcha';
import Box from '@material-ui/core/Box';
import LinkUI from '@material-ui/core/Link';
import { URLGRAPH } from '../constants'

// Redux
import {connect} from 'react-redux'
import {change} from '../redux/actions/Login'

//let jwt = window.localStorage.getItem("token");

function MadeWithLove() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Intermodalidapp & '}
            <LinkUI color="inherit" href="https://material-ui.com/">
                Material-UI
            </LinkUI>
        </Typography>
    );
}

const styles = theme => ({
    root: {
      height: '100vh',
    },
    image: {
      backgroundImage: 'url(https://i.imgur.com/xmKwOXk.jpg)',
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    },
    paper: {
      margin: theme.spacing(8, 4),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.primary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
      textDecorationLine: 'none',
    },
    textoButton: {
      color: 'white',
      textDecorationLine: 'none',
    },
  
})

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            correo: '',
            password: '',
            token: '',
            rol: '',
            isAuth: false,
        }
        this.sendReq = this.sendReq.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.other = this.other.bind(this);
    }
    async handleInputChange(event) {

        const target = event.target.name;
        const value = event.target.value;
        var md5 = require('md5');
        switch(target){
            case "password":
                await this.setState({ password: md5(value)})
            break;
            default:
                await this.setState({ correo: value})
            break;
        }
        
    }
    
    componentWillMount() {
        if (window.localStorage.getItem("token") != null) {
            /*if(this.state.rol === 'generador'){
                this.props.history.push("/user")
            }else{
                this.props.history.push("/services")
            }*/
            //this.props.history.push("/services")
        }
    }

    async other(){
        const axios = require("axios")
        const headers = {
            'Content-Type': 'text/plain',
            'Access-Control-Allow-Origin': 'http://localhost:3000',
        };
        await axios.get('https://servicesima.azurewebsites.net/services',{
                /*headers: {
                    'Access-Control-Allow-Origin': 'http://localhost:3000',
                },*/
                headers
            })
            .then(function (response) {
                console.log(response);
            })
    }
    async sendReq(){

        const axios = require("axios")
        console.log("sendReq: ",this.state.correo)

        await axios.post(URLGRAPH, {
            query: `query{
                userByEmail(email:"${this.state.correo}"){
                    nameE email rol
                }
            }`
         
        })
         .then((result)=>{
            console.log("request")
            let user = result.data.data.userByEmail
            console.log("User: ",user)
            localStorage.setItem('username',user.nameE)
            localStorage.setItem('email',user.email)
            this.setState({
               rol: user.rol
            })   
            console.log("email: ", this.state.correo)
        })
            .catch(err => console.log(err))
        
            
        // autenticación ante register
        await axios.post(URLGRAPH,{
            query: `mutation{
                authUser(credentials: {
                    email: "${this.state.correo}",
                    passw: "${this.state.password}"
                }){
                    token
                    success
                }
            }`    
        }).then((result)=>{
            
            console.log(result.data.data.authUser)
            const success = result.data.data.authUser.success
            
            if(success === true){
                let jwt = result.data.data.authUser.token
                this.props.change(true)
                //alert("hi!")
                window.localStorage.setItem("token", jwt.replace("",''))
                if(this.state.rol === 'generador'){
                    this.props.history.push("/3asdegUser@adsgegwgaggw2354")
                }else{
                    this.props.history.push("/1hnjdeServices@ghaebvrb1256")
                }
                console.log(this.state.token)
            }else{
                alert("Inicio de sesión incorrecto.")
            }
        }).catch(err => console.log(err))

    }
    changeView(){
        this.props.history.push("/register")
    }
    render(){
        const { classes } = this.props;
        const { login } = this.props;
        //console.log("classe of login-->> ",login)
        return(
            <Grid container item spacing={4} component="main" className={classes.root}>
                <CssBaseline />
                <Grid item xs={false} sm={7} md={8} className={classes.image} />
                <Grid item xs={12} sm={5} md={4} component={Paper} elevation={6} square>
                    <div className={classes.paper}>
                        <Avatar className={classes.avatar}>
                            <LockOutlinedIcon />
                        </Avatar>
                        <Typography component="h1" variant="h5">
                            Inicio de sesión
                        </Typography>
                        <form className={classes.form} noValidate>
                            <Grid container item spacing={4} justify="center">
                                <Grid item xs={11} sm={12}> 
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="correo"
                                        label="Correo"
                                        name="correo"
                                        autoFocus
                                        onChange={this.handleInputChange}
                                    />
                                </Grid>
                                <Grid item xs={11} sm={12}> 
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        type="password"
                                        id="password"
                                        label="Contraseña"
                                        name="password"
                                        onChange={this.handleInputChange}
                                    />   
                                </Grid>
                                <Grid item xs={12} sm={12}> 
                                    <Grid container>
                                        <Grid item xs={8} sm={8}>
                                            <a href="" variant="body2">
                                                {"¿No puede ingresar?"}
                                            </a>
                                        </Grid>
                                        <Grid item xs={4} sm={4}>
                                            <FormControlLabel
                                                control={<Checkbox value="remember" color="primary" />}
                                                label="Recuérdame"
                                            />
                                        </Grid>
                                        
                                    </Grid>     
                                </Grid>
                                <Grid item xs={6} sm={6}> 
                                    <Button
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        className={classes.submit}
                                        onClick={this.sendReq}>
                                        Iniciar sesión
                                    </Button>
                                </Grid>

                                <Grid item xs={6} sm={6}> 
                                    <Button
                                        fullWidth
                                        variant="outlined"
                                        color="primary"
                                        className={classes.submit}
                                        onClick={this.changeView.bind(this)}>
                                        Registrarse
                                    </Button>
                                </Grid>

                                <Grid item xs={12} sm={12}> 
                                    <Box mt={1}>
                                        <MadeWithLove />
                                    </Box> 
                                </Grid>
                                
                                
                                
                                
                            </Grid>
                            
                        </form>
                    </div>
                </Grid>
            </Grid>
        );
    }
}

const mapStateToProps = (state) => {
    return {
       login: state.Login,
    };
};
 
const mapDispatchToProps = (dispatch) => {
    return{
       change: (option) => {dispatch(change(option))}
    }
}
export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(Login)));